@import url(https://fonts.googleapis.com/css?family=Barlow:100,200,300,400,500,600,700,800,900);
html {
  font-size: 62.5%; }

body {
  font-family: "Barlow", sans-serif;
  font-weight: 500;
  font-size: 1.6rem;
  background: #f2f2f2; }

.container {
  display: flex;
  justify-content: center; }

.box {
  width: 40%;
  display: flex;
  flex-direction: column;
  padding: 0px 25px 25px 25px; }

.box_fav_list_hide {
  width: 0%; }

.box_fav_list_show {
  width: 30%; }

.header {
  background-color: yellow;
  height: 60px; }

h1 {
  margin: 0;
  padding: 10px;
  text-align: center;
  font-weight: 700; }

.fav_list_button {
  position: absolute;
  right: 0;
  top: 0;
  background-color: black;
  color: white;
  height: 60px;
  font-weight: 600;
  outline: none;
  cursor: pointer; }

.search_area_box {
  text-align: center;
  padding-bottom: 10px; }

h3 {
  font-weight: 700;
  text-align: center; }

input[type="text"] {
  border: none;
  outline: none; }

.search_button {
  border: none;
  outline: none;
  background: #cbcbcb;
  cursor: pointer; }

.result_item_box {
  display: flex;
  background: white;
  margin: 8px; }

.result_item_box:hover {
  border: #ff0081 solid 2px; }

.result_item_link {
  text-decoration: none;
  color: black; }

.description_box {
  display: block;
  padding-left: 15px;
  padding-right: 15px; }
  .description_box .title {
    font-weight: bold;
    font-size: 1.6rem;
    margin-bottom: 3px;
    margin-left: 5px; }
  .description_box .artist {
    margin: 5px; }

.main_content {
  clear: both;
  white-space: pre-line; }

.video {
  width: 100%; }

.lyrics {
  padding-left: 10%;
  padding-right: 10%;
  padding-bottom: 10%; }

.lyrics_box {
  width: 100%;
  background: white;
  border-radius: 5px; }

.no_fav_items_message {
  text-align: center; }

.fav_item_box {
  display: flex;
  position: relative;
  height: 5em;
  background: white;
  margin: 5px; }

.fav_item_box:hover {
  border: #ff0081 solid 2px; }

.fav_thumb {
  width: 5em;
  height: 5em; }

.fav_description_box {
  font-size: 1.3rem;
  padding: 5px;
  overflow: hidden;
  white-space: break-word;
  text-overflow: ellipsis; }
  .fav_description_box p {
    margin: 3px; }

.remove_fav_item_button {
  background: #f2f2f2;
  border: none;
  cursor: pointer;
  outline: none;
  float: right; }

.fa-times-circle {
  font-size: 1.2em; }

.fav_button:visited {
  background: black; }

.fav_button {
  height: 30px;
  width: 40px;
  float: right;
  font-family: "Helvetica", "Arial", sans-serif;
  display: inline-block;
  font-size: 1em;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  background-color: black;
  margin-bottom: 5px;
  color: white;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  position: relative; }
  .fav_button:focus {
    outline: 0; }
  .fav_button:before, .fav_button:after {
    position: absolute;
    content: "";
    display: block;
    width: 140%;
    height: 100%;
    left: -20%;
    z-index: -1000; }
  .fav_button:before {
    display: none;
    top: -75%;
    background-size: 10% 10%, 20% 20%, 15% 15%, 20% 20%, 18% 18%, 10% 10%, 15% 15%, 10% 10%, 18% 18%;
    background-position: 0% 80%, -5% 20%, 10% 40%, 20% 0%, 30% 30%, 22% 50%, 50% 50%, 65% 20%, 85% 30%; }
  .fav_button:after {
    display: none;
    bottom: -75%;
    background-size: 15% 15%, 20% 20%, 18% 18%, 20% 20%, 15% 15%, 10% 10%, 20% 20%;
    background-position: 5% 90%, 10% 90%, 10% 90%, 15% 90%, 25% 90%, 25% 90%, 40% 90%, 55% 90%, 70% 90%; }
  .fav_button:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }

